@font-face {
  font-weight: normal;
  font-family: "Open Sans";
  font-style: normal;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
}
@font-face {
  font-weight: bold;
  font-family: "Open Sans";
  font-style: normal;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
}
html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
body {
  min-width: 320px;
  margin: 0;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 17px;
  line-height: 24px;
  font-weight: bold;
  color: #000000;
}
a {
  color: #000000;
  text-decoration: none;
}
img {
  max-width: 100%;
  height: auto;
}
.container {
  width: 320px;
  padding: 0 30px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container {
    width: 768px;
    padding: 0 50px;
  }
}
@media (min-width: 1150px) {
  .container {
    width: 1150px;
  }
}
.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page-header {
  display: flex;
  flex-direction: column;
  background-color: #f9f9f9;
}
.page-header--inner {
  background-color: #f1f1f1;
}
.page-header::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 395px;
  background-color: white;
  border-bottom: 1px solid #ececec;
}
.page-header__inner {
  width: auto;
  margin: 0;
  padding: 0;
  z-index: 1;
}
.header-top__logo {
  display: block;
  padding-top: 18px;
  padding-bottom: 18px;
  position: relative;
  text-align: center;
  border-bottom: 1px solid #ececec;
}
.logo__image {
  width: 86px;
  height: 35px;
}
.logo__image:hover {
  opacity: 0.6;
}
.logo__image:active {
  opacity: 0.3;
}
.header-top__toggle-close,
.header-top__toggle-open {
  padding: 0;
  position: absolute;
  top: 27px;
  right: 67px;
  background-color: white;
  border: none;
}
.header-top__toggle-close {
  display: none;
}
.header-top__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.bottom-list__item,
.top-list__item {
  padding: 27px 0;
  text-align: center;
  border-bottom: 1px solid #ececec;
}
.bottom-list__item:hover .bottom-list__link,
.top-list__item:hover .top-list__link,
.additional-list__item:hover .additional-list__link {
  color: rgba(0, 0, 0, 0.6);
}
.bottom-list__item:active .bottom-list__link,
.top-list__item:active .top-list__link,
.additional-list__item:active .additional-list__link {
  color: rgba(0, 0, 0, 0.3);
}
.top-list__item--search:hover .top-list__search-icon {
  fill: #63d1bb;
  opacity: unset;
}
.top-list__item--search:active .top-list__search-icon {
  opacity: 0.3;
}
.top-list__item--search {
  padding-right: 20px;
}
.top-list__item--search a {
  position: relative;
}
.top-list__search-icon {
  position: absolute;
  top: 4px;
  left: -40px;
  width: 17.6px;
  height: 17.6px;
  opacity: 0.2;
}
.top-list__item--basket:hover .top-list__basket-icon {
  fill: #63d1bb;
  opacity: unset;
}
.top-list__item--basket:active .top-list__basket-icon {
  opacity: 0.3;
}
.top-list__item--basket a {
  position: relative;
}
.top-list__basket-icon {
  position: absolute;
  top: 4px;
  left: -40px;
  width: 21px;
  height: 18px;
  opacity: 0.2;
}
.header-top__delivery {
  display: none;
}
.additional-list {
  display: none;
}
@media (min-width: 768px) {
  .page-header::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 88px;
    z-index: 1;
    background-color: white;
    border-bottom: 1px solid #ececec;
  }
  .page-header__inner {
    width: 768px;
    margin: 0 auto;
    padding: 0 50px;
  }
  .header-top {
    display: flex;
    flex-wrap: wrap;
  }
  .header-top__logo {
    margin-right: auto;
    padding-top: 22px;
    padding-bottom: 0;
    padding-left: 0;
    z-index: 2;
  }
  .logo__image {
    width: 112px;
    height: 46px;
  }
  .header-top__toggle {
    display: none;
  }
  .header-top__toggle-close {
    display: none;
  }
  .header-top__toggle-open {
    display: none;
  }
  .header-top__list {
    display: flex;
  }
  .bottom-list__item,
  .top-list__item {
    padding: 0;
    text-align: start;
  }
  .bottom-list {
    order: 1;
    font-size: 15px;
    line-height: 24px;
  }
  .bottom-list,
  .header-top__delivery {
    padding-top: 20px;
  }
  .bottom-list__item {
    padding-right: 34px;
    border-bottom: none;
  }
  .top-list {
    order: 0;
    z-index: 2;
  }
  .top-list__item--search {
    width: 83px;
    font-size: 0;
    border-right: 1px solid #ececec;
    border-left: 1px solid #ececec;
  }
  .top-list__search-icon {
    top: 24px;
    left: 31px;
  }
  .top-list__item--basket {
    padding-top: 31px;
    padding-bottom: 32px;
    padding-left: 95px;
  }
  .header-top__delivery {
    display: block;
    margin: 0;
    margin-left: auto;
    order: 2;
    font-size: 15px;
    line-height: 24px;
    font-weight: normal;
    color: #444444;
  }
}
@media (min-width: 1150px) {
  .page-header::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 89px;
    z-index: 1;
    background-color: white;
    border-bottom: 1px solid #ececec;
  }
  .page-header__inner {
    width: 1150px;
  }
  .header-top__logo {
    width: 256px;
    margin-right: 0;
    padding-top: 9px;
    order: 1;
    text-align: start;
    z-index: 2;
    border-bottom: none;
  }
