    width: 149px;
    height: 72px;
  }
  .bottom-list {
    padding-left: 0;
    padding-top: 0;
    order: 0;
    z-index: 2;
    background-color: inherit;
  }
  .bottom-list__item {
    padding-top: 30px;
    padding-bottom: 34px;
    font-size: 17px;
  }
  .bottom-list__item--catalog {
