    border-right: 1px solid #ececec;
  }
  .bottom-list__item--form {
    width: 258px;
    padding-left: 34px;
    padding-right: 0;
  }
  .top-list {
    order: 2;
    z-index: 2;
  }
  .top-list__item {
    border-bottom: none;
  }
  .top-list__item--search {
    width: 83px;
  }
  .top-list__item--basket {
    width: 267px;
    padding-top: 30px;
    padding-left: 85px;
  }
  .header-top__delivery {
    order: 4;
    margin-left: auto;
  }
  .header-top__delivery,
  .additional-list__item {
    padding-top: 20px;
  }
  .additional-list {
    display: flex;
    order: 3;
    margin: 0;
    padding: 0;
    list-style: none;
    font-size: 15px;
    line-height: 24px;
